<template>
  <main class="login">

    <section class="page-title-section theme-bg">
      <div class="container">
        <div class="page-title">
          <h1>Reset Password</h1>
          <ol class="breadcrumb justify-content-center">
              <li><router-link to="/">Home</router-link></li>
              <li class="active">Reset Password</li>
          </ol>
        </div>
      </div>
    </section>

    <section>
      <div class="container container-large">
        <div class="row align-items-center sm-p-30px-b">
          
          <div class="col-md-6 offset-md-3 col-lg-4 offset-lg-4 right-img md-m-30px-t">
            <div class="contact-form ">
              <img  class="user-avatar-cred" src="../../assets/static/img/user-icon.svg" alt="user sign in">
              <form class="contactform py-3" method="post"  v-on:submit.prevent="onSubmit" >
                <div class="row">
                  <div class="col-md-12">
                    <div class="form-group">
                      <input id="email" type="email" v-model="user.email" placeholder="Email" name="email" class="form-control" required>
                    </div>
                  </div>
                  
                  <div class="col-md-12">
                    <div class="send">
                      <button class="btn btn-primary" type="submit" name="send"> Reset Password</button>
                    </div>
                    <div class="clearfix"></div> <br/>
                      <p class="gray-dark">Back to login? <router-link to="login">Login</router-link> </p>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div> 
      </div> 
    </section>

  </main>
</template>

<script>
  import * as auth from '../../services/auth_service'
  export default {
    name: 'forgot',
    data(){
      return{
        user: {
              email: '',
              
            },
         errors: {}
      }
    },
    methods: {
        onSubmit: async function() {
        
          await auth.forgot(this.user)
           .then(response => {
               console.log("up "+this.response.data)
             this.flashMessage.success({
                      title: 'Success',
                      message: "An email has been sent to you",
                      time: 7000,
                      icon: '/img/user-dummy.52069854.jpg'
                  });
            //this.errors = {};
                    this.$store.state.commonMessage = 'passwordrest'
                    this.$router.push({ name: 'home' });
          })
          .catch(error => {
//console.log("below "+this.response.data)
              if (error.response.data.errors.message) {
                  this.errors = {};
                  this.flashMessage.error({
                      title: 'Error',
                      message: "There was an error",//error.response.data.errors.message,
                      time: 7000,
                      icon: '/img/user-dummy.52069854.jpg'
                  });
              } else {
                  this.errors = error.response.data.errors;
              }
          }); 
          
        }
      }
  }
</script>
